@font-face {
  font-family: 'IRANYekanWebRegular';
  src: url('/src/assets/fonts/IRANYekanWebRegularNum.woff'); /* IE9 Compat Modes */
  src: url('/src/assets/fonts/IRANYekanWebRegularNum.woff') format('embedded-opentype'), /* IE6-IE8 */
       url('/src/assets/fonts/IRANYekanWebRegularNum.woff') format('woff2'), /* Super Modern Browsers */
       url('/src/assets/fonts/IRANYekanWebRegularNum.woff') format('woff'), /* Pretty Modern Browsers */
       url('/src/assets/fonts/IRANYekanWebRegularNum.woff')  format('truetype'), /* Safari, Android, iOS */
       url('/src/assets/fonts/IRANYekanWebRegularNum.woff') format('svg'); /* Legacy iOS */
}

* {
  font-family: 'IRANYekanWebRegular' !important;
  }

body{
  background-color:#F7F8F9 !important;
}
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #25233c; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.4); 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; 
}

.highcharts-container {
  margin: auto;
}
@keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}

.dashExport {
  stroke-dasharray: 3;
  stroke: #2ABB7F;
  stroke-width: 0.45px;
  stroke-dashoffset: 100;
   animation: dashExport 10s linear infinite;
}
.dashImport {
  stroke-dasharray: 3;
  stroke: #EF5C48;
  stroke-width: 0.45px;
  stroke-dashoffset: 100;
   animation: dashImport 10s linear infinite;
}
@keyframes dashExport {
  from {
      stroke-dashoffset: 100;
  }

  to {
      stroke-dashoffset: 20;
  }
}
@keyframes dashImport {
  from {
      stroke-dashoffset: 20;
  }

  to {
      stroke-dashoffset: 100;
  }
}
.highcharts-mapline-series .highcharts-point{

}
.highcharts-axis-labels{
  font-size: 12px;
}
.highcharts-axis{
  font-size: 12px;
}
.highcharts-legend-item{
  font-size: 16px;
}
.tooltip {
  position: relative;
  display: inline-block;
  border-bottom: 1px dotted black;
}

.tooltiptext {
  visibility: hidden;
  width: 120px;
  background-color: #555;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;
  position: absolute;
  z-index: 1;
  bottom: 125%;
  left: 50%;
  margin-left: -60px;
  opacity: 0;
  transition: opacity 0.3s;
}

.tooltiptext::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: #555 transparent transparent transparent;
}

.tooltip:hover .tooltiptext {
  visibility: visible;
  opacity: 1;
}
/* .tooltipvalue .tooltip:hover .tooltipvalue  {
  visibility: visible;
  opacity: 0;
} */
.tooltipvalue {
  pointer-events: none; /* disables the hover on the .parent                 */
}

.tooltipvalue:hover {           /* styles for parent element when hover is on .child */
  display: none;
}

.tooltipvalue .tooltip {
  pointer-events: auto; /* enables the hover on the .parent                  */
}

.tooltipvalue .tooltip:hover {    /* styles for child element when hover is on .child  */
  display: none;
}

.highcharts-organization-series > .highcharts-tracker:has(span):has(.tree-node){
  box-shadow: 0px 2px 8px rgba(255, 159, 64, 0.16);
  border-radius: 3px;
  width: 140px !important;
  height: 60px !important;
}